@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    
}
.blog{ 
    box-shadow: -1px 3px 15px 5px rgba(0,0,0,0.38);
    padding: 4rem;
    border-radius: 25px;
    width:"70vw";
    height:"30vh";
    margin-top: 6rem;
    text-align: left;
}

.blog-title{
    font-weight: bold;
    font-size: larger;
}

.blog-content{
    margin-top: 1.5rem;
    width: 70%;
}

.read-more{
    width: 100%;
    display: flex;
    justify-content: right;
    text-transform: capitalize;
    color:lightskyblue;
    font-weight: bolder;
    margin-top: 1rem;
}
.heading{
    font-size: 2.5rem;
}
.subheading{
    font-size: 1.5rem;
    margin-top: 2rem;
}
.subHeading2{
    margin-top: 3rem;
    font-size: 1.7rem;
}

.box{
    box-shadow: -6px -4px 14px -3px rgba(0,0,0,0.42);
    -webkit-box-shadow: -6px -4px 14px -3px rgba(0,0,0,0.42);
    -moz-box-shadow: -6px -4px 14px -3px rgba(0,0,0,0.42);
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 200px;
    flex-direction: column;
    border-radius: 25px;
}
.footer{
    width: 100vw;
    height: 30vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 2rem;
    background-color:#0E79BD ;
}

.logo{
    width:10%;
}

.lists{
    color: white;
    display: flex;
    justify-content: space-between;
    width: 20vw;
    line-height: 30px;
    font-weight: bold;
}

.copyright{
    width: 100vw;
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 2rem;
    background-color:#0E79BD ;
    font-size: smaller;
}


.video{
    width:"100vw";
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 6rem;
}

.heading{
    font-size: 2.5rem;
}
.topic-box{
    margin-top: 6rem; 
    box-shadow: -1px 3px 15px 5px rgba(0,0,0,0.38);
    text-align: left;
    border-radius: 25px;
    background-image: url(/proffus/static/media/topic2.617c68ca.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 30vh;
}
.topic-title{
    font-weight: bold;
    font-size: larger;
}

.topic-content{
    margin-top: 1.5rem;
    width: 50%;
}

.tread-more{
    text-transform: capitalize;
    color:lightskyblue;
    font-weight: bolder;
    margin-top: 2rem;
}

.gradient{
    height: 30vh;
    border-radius: 25px;
    width: 100%;
    padding: 4rem;
    background-image: linear-gradient(to right,white,rgba(255,255,255,0.3));
}
